//variables
@import "variables";
.navbar{
	margin-bottom: 0px;
}
.big-padding{
	padding:30px;
}

.blue-grey{
	background-color:#37474f;
}
.white-text{
	color:#fff;
}

.table{
	background-color:#fff;
}

.floating{
	position:fixed;
	bottom:2em;
	right:2em;
	z-index:100;
}

.product{
	width:600px;
	max-width:100%;
	padding:1em;
	position:relative;

		h1{
			padding-bottom:10px;
			border-bottom:solid 1px;
		}
	
}

.red-text
{
	color:rgb(200,50,50);
}
.no-padding{
	padding:0px;
}

.no-margin{
	margin:0px;
}

.inline-block{
	display:inline-block;
}

.sale-data{
	padding:1em;
	text-align: center;
	border-right: solid 1px #222;
	span{
		display: block;
		font-size: 2.2em;
		color: #3f51b5;
	}
}

#order{
	.row{
		padding:10px;
	}
}
